#map {
  position: relative;
  background-color: #fff;
  width: 100%;
  min-width: 970px;
  height: 880px;

  .map-box {
    position: absolute;
    right:0;
    margin: 30px;
    width: 490px;
    //height: 785px;
    z-index: 1;
    background-color: #fff;
    background-color: rgba(255,255,255,.8);
    border-radius: 5px;
    -webkit-box-shadow: 0 6px 12px rgba(0,0,0,.175);
    box-shadow: 0 6px 12px rgba(0,0,0,.175);

    & .help-text {
      color: #e8290c;
      padding: 15px;
      font-size: 12px;
    }

    &__list-bib {
      max-height: 430px;
      margin: 10px;
      overflow-y: auto;
      border: 1px solid #f2f2f2;
      border-radius: 2px;

      &__item {
        border-bottom: 1px solid #f2f2f2;
        border-radius: 2px;
        padding: 25px;
        font-family: $font-stack;
        font-size: 18px;
        font-weight: 400;
        line-height: 26px;

        &__link {
          color: #000;
          text-decoration: underline;
        }

        & span {
          color: #d1d1d1;
        }

        &:hover .map-box__list-bib__item__link, &:hover span {
          color: $hover_color;
        }

        &:last-child {
          border-bottom: none;
        }

        &__btn-box {
          display: block;
          margin: 10px 0;
        }

        &__btn {
          display: inline-block;
          font-weight: 300;
          font-size: 14px;
          color: #fff;
          padding: 3px 15px;
          position: relative;
          text-decoration: none;
          background-color: #1d609b;
          border-radius: 4px;

          &:hover {
            background-color: $hover_color;
          }

          &:active {
            background: #d0250c;
          }

        }

      }

      &__detail {

        &__btn-back {
          display: inline-block;
          font-family: $font-stack;
          font-weight: 300;
          font-size: 14px;
          color: #fff;
          padding: 3px 15px;
          position: relative;
          text-decoration: none;
          background-color: #1d609b;
          border-radius: 4px;
          margin: 30px 0 0 30px;

          &:hover {
            background-color: $hover_color;
          }

          &:active {
            background: #d0250c;
          }
        }

        &__title {
          margin: 30px 30px 0 30px;
          font-family: $font-stack;
          color: #1b1b1b;
          font-weight: 500;
          font-size: 30px;
        }

        &__title-info {
          margin: 0;
          padding: 30px;
          font-family: $font-stack;
          font-size: 18px;
          font-weight: 400;
          line-height: 26px;
          border-bottom: 1px solid #f2f2f2;

          & a {
            color: #000;
            text-decoration: underline;

            &:hover {
              color: $hover_color;
            }

          }
        }

      }

    }

    &__title {
      display: block;
      margin: 20px 10px 20px 10px;
      font-family: $font-stack;
      color: #363636;
      font-weight: 500;
      font-size: 17px;
    }

    .adrr-search {
      position: relative;
      margin: 0 10px;

      & input {
        font-family: $font-stack;
        font-size: 18px;
        font-weight: 400;
        border: 1px solid #e5e5e5;
        border-radius: 2px;
        color: #363636;
        width: 100%;
        padding: 15px;
      }

      & .tt-dropdown-menu {
        position: absolute;
        overflow: auto;
        top: 60px;
        width: 100%;
        border: 1px solid #e5e5e5;
        border-radius: 2px;
        background-color: #fff;
        z-index: 2;
        max-height: 400px;
        box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
      }

      & .tt-suggestion {
        padding: 0 15px;
        cursor: pointer;
        text-decoration: underline;

        &:hover {
          color: #e8290c;
        }

      }

      & .twitter-typeahead {
        width: 100%;
      }

    }

    .abc-crumbs {
      display: block;
      margin: 10px;

      &__list {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
          //position: relative;
          margin: 0;
          padding: 0;
          display: inline-block;
          line-height: 29px;
          text-align: center;
        }

        &__hover-box {
          display: none;
          position: fixed;
          height: auto;
          width: 300px;
          z-index: 2;
          background-color: #fff;
          border-radius: 5px;
          box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
          font-family: Roboto,sans-serif;
          font-size: 18px;
          font-weight: 400;
          line-height: 26px;
          text-align: left;

          &_show {
            display: block;
          }

          &:after {
            position: absolute;
            top: -10px;
            right: 30px;
            bottom: 100%;
            left: auto;
            margin-left: -10px;
            border-top: none;
            border-right: 10px solid transparent;
            border-bottom: 10px solid #fff;
            border-left: 10px solid transparent;
            content: "";
          }

          & .map-box__list-bib {
            height: auto;
            margin-bottom: 10px;
            font-size: 14px;

            &__item {
              font-size: 14px;
              padding: 10px;
            }

          }

        }

        &_link {
          display: inline-block;
          width: 69px;
          height: 69px;
          padding-top: 18px;
          margin: 0 5px 10px 0;
          background-color: #fff;
          font-family: $font-stack;
          color: #363636;
          font-weight: 500;
          font-size: 36px;
          user-select: none;
          text-decoration: none;
          border: 1px #e5e5e5 solid;
          border-radius: 2px;
          white-space: nowrap;
          cursor: pointer;
          z-index: 1;

          &-img {
            display: inline-block;
            width: 69px;
            height: 69px;
            padding-top: 5px;
            margin: 0 5px 10px 0;
            background-color: #fff;
            border: 1px #e5e5e5 solid;
            border-radius: 2px;

            img {
              position: relative;
              top: 10px;
              width: 40px;
              height: 40px;
            }

            &:hover {
              color: $hover_color;
              border-color: $hover_color;
            }


          }

          &:hover {
            color: $hover_color;
            border-color: $hover_color;
          }

        }

      }

    }


  }

}

