.facet {

  &__header {
    position: relative;
    padding: 23px 23px 23px 50px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

//    &:hover {
//      background-color: #f7f7f7;
//    }
//
//    &:active {
//      background-color: #f1f1f1;
//    }

    &:after {
      position: absolute;
      top: 50%;
      left: 20px;
      width: 10px;
      height: 10px;
      margin-top: -5px;
      content: "";
      text-align: center;
      background-image: url('../images/selectdown.svg');
      background-repeat: no-repeat;
      background-position: 50% 50%;
    }

    &_closed {

      &:after {
        transform: rotate(180deg);
      }

    }
  }

  &__header:hover &__title {
    color: $hover_color;
  }

  &__title {
    position: relative;
    margin: 0;
    font-family: $font-stack;
    color: #363636;
    font-weight: 500;
    font-size: 30px;
  }

  &__body {
    padding: 0 50px 50px 50px;

    &_closed {
      display: none;
    }


    &__news {
      font-family: $font-stack;
      padding: 30px 0;
      border-bottom: 1px solid #d8dee3;

      &:last-child {
        border-bottom: none;
        padding-bottom: 0;
      }

      &__title {
        display: block;
        margin: 0;
        font-weight: 400;
        font-size: 18px;
        line-height: 26px;

        & a {
          text-decoration: none;
          border-bottom: 1px solid #2f61a4;
          color: #2f61a4;
        }

        &:hover {
          color: $hover_color;
        }

        &:hover a {
          color: $hover_color;
          border-color: $hover_color;
        }
      }

      &__date {
        margin-top: 10px;
        font-size: 12px;
        color: #9da2a6;
      }

    }

  }

  &__link {
    display: block;
    margin-bottom: 30px;
    font-family: $font-stack;
    font-weight: 300;
    font-size: 18px;
    line-height: 26px;
    color: #b0b0b0;

    & a {
      text-decoration: none;
      border-bottom: 1px solid #2f61a4;
      color: #2f61a4;
    }

    &:hover {
      color: $hover_color;
    }

    &:hover a {
      color: $hover_color;
      border-color: $hover_color;
    }

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__checkbox {
    position: relative;
    display: block;
    color: #444;
    font-size: 0.875em;
    font-weight: 300;
    margin-bottom: 5px;

    label {
      display: block;
      max-width: 100%;
      cursor: pointer;
      padding-left: 20px;
    }

    input[type=checkbox] {
      position: absolute;
      margin-left: -20px;
    }
  }

  &__radio {
    position: relative;
    display: block;
    color: #444;
    font-size: 0.875em;
    font-weight: 300;
    margin-bottom: 5px;

    label {
      display: block;
      max-width: 100%;
      cursor: pointer;
      padding-left: 20px;
    }

    input[type=radio] {
      position: absolute;
      margin-left: -20px;
    }
  }

  &__input {
    position: relative;
    display: block;
    color: #444;
    font-size: 0.875em;
    font-weight: 300;

    label {
      display: block;
      max-width: 100%;
      cursor: pointer;
    }

    &_line {

      input {
        display: inline-block;
        position: relative;
        width: 100%;
        background-color: #fff;
        border: 1px #d7d7d7 solid;
        padding: 5px;
        border-radius: 3px;
        outline: none;
        margin-bottom: 5px;

        &:focus {
          border-color: #2196F3;
        }
      }

    }

    &_group {

      input {
        display: inline-block;
        position: relative;
        width: 49%;
        background-color: #fff;
        border: 1px #d7d7d7 solid;
        padding: 5px;
        border-radius: 3px;
        outline: none;
        margin-bottom: 5px;

        &:focus {
          border-color: #2196F3;
        }

        &:last-child {
          float: right;
        }

      }

    }
  }

  &__btn {
    position: relative;
    text-align: center;
    text-decoration: none;
    display: block;
    width: 100%;
    font-size: 0.875em;
    font-weight: 300;
    background-color: #fff;
    color: #444;
    border: 1px #b1b1b1 solid;
    border-radius: 3px;
    padding: 5px 10px;
    outline: none;

    &:hover {
      background-color: #f7f7f7;
    }

    &:active {
      background-color: #f1f1f1;
    }
  }

  &__slider {
    width: 100%;
    height: 20px;
    margin-bottom: 5px;
    z-index: 0;

    & .handle {
      font-size: 0.9em;
      text-align: center;
      background-color: #fff;
      color: white;
      cursor: pointer;
      border: 1px solid #909eab;
      border-radius: 50%;
      z-index: 0;
    }

    .handle.active {
      background-color: #f1f1f1;
      border-color: #2196F3;
    }

    .bar {
      position: relative;
      background: #ddd;
      cursor: pointer;
    }

    .bar.bar-1 {
      background: #b1b1b1;
    }

    .bar.bar-2 {
      background: #ddd;
    }

    & .bar {
      top: 8px;
      height: 1px;
    }

    & .handle {
      width: 18px;
      height: 18px;
    }

  }
}

