.school {
  margin: 20px 45px 45px;
  font-family: $font-stack;

  & p {
    font-size: 18px;
    font-weight: 400;
    color: #000;
    line-height: 32px;

    //& a {
    //  color: #23589e;
    //
    //  &:hover {
    //    color: #c5c5c5;
    //  }
    //
    //}

  }

}

.school-list {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0 0 40px 0;
  list-style: none;

  &__item {
    margin-right: 25px;

    &:last-child {
      margin-right: 0;
    }

  }

  &__link {
    font-size: 20px;
    font-weight: 300;
    color: #000;

    &:hover {
      color: #c5c5c5;
      border-bottom-color: #c5c5c5;
    }

    &_active {
      color: #000;
      padding: 8px 15px;
      border: 4px solid #c5c5c5;
      border-radius: 25px;
      cursor: default;
      text-decoration: none;
    }

  }

}